/* eslint-disable react/jsx-one-expression-per-line */
import React, { Component } from "react";
import photo from "./me-12-12-19.jpg";

class About extends Component {
  render() {
    return (
      <div className="about">
        <div className="hero">
          <h1>
            About me
          </h1>
          <p>
            My name’s Edwin and I’m a student.
          </p>
        </div>

        <div className="columns one">
          <div className="panel">
            <h2>
              My Skillset
            </h2>
            <p>wait what am I good at?</p>
            <ul className="left-list">
              <h3>Proficent:</h3>
              <li>Python</li>
              <li>HTML5</li>
              <li>CSS3</li>
              <li>JavaScript (ES6, React)</li>
            </ul>
            <ul className="right-list">
            <h3>Comfortable with:</h3>
              <li>C/C++</li>
              <li>SQL(PostgreSQL)</li>
              <li>Java</li>
              <li>Version Control (GitHub)</li>
            </ul>
          </div>
        </div>
        <div className="columns two">
          <div className="panel">
            <h2>
              How I got started
            </h2>
            <div className="bio">
              <p>
                After studying Computer Science,... i made this lul
              </p>
              <p>
                This website was a byproduct of me wanting to show projects I make off. 
              </p>
            </div>
          </div>
          <img className="photo" src={photo} alt="Me in October of 2018" />
        </div>
      </div>
    );
  }
}

export default About;
